import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import getters from './getters'
import base from './modules/base'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  modules: {
    base
  },
  actions,
  getters,
  strict: debug
})



